		box-shadow: 0 0 5px #CCCCCC;
		position: absolute;
		top: 6.82%;
		left: 7.5%;
	}
	#textInf{
		position: relative;
		top: 185px;
		left: 108px;
		font-family: "微软雅黑";
		font-size: 30px;
		font-weight: 600;
		color: #515151;
	}
	#nameLine{
		position: relative;
		top: 190px;
		right: 452px;
		width: 150px;
	}
	router-view{
		position: absolute;
		top: 10%;
		left: 40%;
		width: 400px;
		height: 400px;
	}
	#information ul{
		position: relative;
		top: 45%;
		left: 9.8%;
	}
	#information li{
		position: absolute;
		font-size: 20px;
		font-weight: 500;
		font-family: "微软雅黑";
	}
	#information a{
		text-decoration:none;
		color: #515151;		
	}
	#inf1{
		top: 0px;
	}
	#inf2{
		top: 50px;
	}
	#inf3{
		top: 100px;
	}
    
</style>


