<template>
	<div id="home">
	<transition name="fadeInf">
		<div id="homeHover" v-show="home"></div>
	</transition>
	<Information></Information>
		
	</div>
</template>

<script>
	import Information from '../components/Information.vue'
	import Card from '../components/Card.vue'
	
	export default{
	name:'home',
	components:{
		Information,
		Card
	},
	data(){
		return{
			home:true
		}
	}
}
</script>

<style>
	#homeHover{
		position: absolute;
		height: 8px;
		width: 70px;
		background-color: #515151;
		left: 5%;
	}
</style>


