<template>
	<div>
		<transition name="fadeInf">
			<div id="showHover" v-show="show"></div>
		</transition>
	</div>
</template>

<script>
	export default{
	data(){
	   	return{
	   		show:true
	   	}
	}
}
</script>

<style>
	#showHover{
		position: absolute;
		height: 8px;
		width: 65px;
		background-color: #515151;
		left: 14.7%;
	}
	
</style>


